/* =================================
  Styles for the hambuger button which
  is the DrawerToggleButton Component
==================================== */

.toggle_button {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: 20px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  
  .toggle_button:focus {
    outline: none;
  }
  
  .toggle_button_line {
    width: 25px;
    height: 3px;
    background: #093a58;
  }
  


